@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Press+Start+2P&display=swap');
@font-face {
	font-family: Kanit-Thin-Italic;
	src: url(../utils/Fonts/Kanit-ThinItalic.ttf);
  }

  @font-face {
	font-family: Good-Times-Regular;
	src: url(../utils/Fonts/Good-Times-Regular.otf);
  }

  @font-face {
	font-family: Kanit-Black;
	src: url(../utils/Fonts/Kanit-Black.ttf);
  }

  @font-face {
	font-family: Kanit-Medium;
	src: url(../utils/Fonts/Kanit-Medium.ttf);
  }

html {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
}

body {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	background: url('../images/about.png') center center fixed;
    background-size: cover;
    background-size: 100%;
	cursor: default;
	font-family: halyard-display, sans-serif;
}

@keyframes rotateIt {
    to {
      transform: rotate(-360deg);
    }
  }

.button--surtur {
	padding: 0;
	background: none;
	-webkit-clip-path: circle(40% at 50% 50%);
	clip-path: circle(40% at 50% 50%);
	top: 300px;
	left: 300px;
	position: relative;
}

.textcircle {
	position: relative;
	display: block;
	width: 200px;
}

.textcircle text {
	font-size: 32px;
	text-transform: uppercase;
	fill: #AEFF00;
}

.textcircle textPath {
	letter-spacing: 17px; /* Firefox needs this */
}

.button--surtur:hover .textcircle {
	animation: rotateIt 7s linear infinite;
}

.link--helike {
    font-family: aktiv-grotesk-extended, sans-serif;
}

.link--helike:hover span {
    animation: glitchText 0.4s linear;
}

@keyframes glitchText {
    0% {
        opacity: 1;
        transform: translate3d(-10px, 0, 0) scale3d(-1, -1, 1);
        -webkit-clip-path: polygon(0 20%, 100% 20%, 100% 21%, 0 21%);
        clip-path: polygon(0 20%, 100% 20%, 100% 21%, 0 21%);
    }

    10% {
        -webkit-clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0 33%);
        clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0 33%);
    }

    20% {
        -webkit-clip-path: polygon(0 44%, 100% 44%, 100% 44%, 0 44%);
        clip-path: polygon(0 44%, 100% 44%, 100% 44%, 0 44%);
    }

    35% {
        -webkit-clip-path: polygon(0 50%, 100% 50%, 100% 20%, 0 20%);
        clip-path: polygon(0 50%, 100% 50%, 100% 20%, 0 20%);
    }

    50% {
        -webkit-clip-path: polygon(0 70%, 100% 70%, 100% 70%, 0 70%);
        clip-path: polygon(0 70%, 100% 70%, 100% 70%, 0 70%);
    }

    60% {
        -webkit-clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
        clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
    }

    70% {
        -webkit-clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
        clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
    }

    80% {
        -webkit-clip-path: polygon(0 70%, 100% 70%, 100% 80%, 0 80%);
        clip-path: polygon(0 70%, 100% 70%, 100% 80%, 0 80%);
    }

    90% {
        transform: translate3d(-10px, 0, 0) scale3d(-1, -1, 1);
    }

    100% {
        opacity: 1;
        transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    }
}

.link--helike::before {
    height: 2px;
    opacity: 0;
}

.link--helike:hover::before {
    opacity: 1;
}

.aboutButton{
	border: none;
	padding: 0;
	background: none;
	transform: rotate(-90deg);
	font-size: 2em;
	position: absolute;
	right: 10px;
	top: 50px;
}

.aboutText{
	padding: 5%;
	padding-top: 3%;
	display: inline-block;
	color: white;
	font-size: 2em;
	will-change: transform, opacity;
	overflow: hidden;
}

.aboutButton-cancel{
	border: none;
	padding: 0;
	background: none;
	position: absolute;
	right: 10px;
	top: 50px;
}

.cancelAbout{
	width: 60px;
}

.logo{
	padding: 1%;
	width:9em;
}

#root {
	overflow: hidden;
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
}
#title {
	display: flex;
	justify-content: center;
	position: relative;
}
h1 {
	padding-left: 5%;
	padding-top: 3%;
	display: inline-block;
	font-size: 8em;
	font-weight: 800;
	text-transform: uppercase;
	will-change: transform, opacity;
	overflow: hidden;
}


.simple-trans-main {
	>div {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		color: white;
		will-change: transform, opacity;
		text-shadow: 0px 2px 40px #00000020, 0px 2px 5px #00000030;
	}
}
